input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px var(--background-color) inset;
  transition: background-color 5000s ease-in-out 0s;
};  

.reservation-banner{
    margin-top: 120px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--primary-color-1);
}

.reservation-banner > h3{
    font-size: 64px;
    font-weight: 400;
    color: var(--primary-color-2);
}

.reservation-banner  > img{
    width: 250px;
    height: 250px;
    border-radius: 50%;
}

/* booking form */
.form-wrapper{
    margin-top: 120px;
    padding: 80px 40px;
}

.form-title{
    text-align: center;
    font-size: 64px;
    font-weight: 400;
    color: var(--primary-color-1);
}

.booking-form{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

.form-input-wrapper{
    width: 45%;
    display: flex;
    position: relative;
    flex-direction: column;
    margin: 16px 16px 0 0;
}

.form-input-wrapper > input,select{
    margin-top: 8px;
    padding: 16px;
    border-radius: 8px;
    border: none;
    font-size: 18px;
    color: #000;
    cursor: pointer;
    background-color: var(--background-color)!important;
}

.form-input-wrapper > input,select,option:focus-visible{
    outline: none;
}

.submit-btn{
    width: 100%;
    margin-top: 28px;
    padding: 16px;
    font-size: 24px;
    font-weight: 400;
    border: none;
    border-radius: 16px;
    cursor: pointer;
    transition: all ease 0.2s;
    background-color: var(--primary-color-2);
}

.submit-btn:hover{
    background-color: var(--primary-color-1);
    color: var(--primary-color-2);
}

/* confirmation page */
.confirm-booking-wrapper{
    margin-top: 120px;
    padding: 40px 40px;
}

.confirm-booking-title{
    font-size: 64px;
    font-weight: 400;
    text-align: center;
    color: var(--primary-color-1);
}

.confirm-booking-wrapper > h4{
    font-size: 40px;
    font-weight: 400;
    text-align: center;
    color: var(--title-color);
}

.booking-details{
    margin-top: 16px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

.booking-details > p{
    font-size: 24px;
    margin-top: 20px;
    width: 48%;
}

.booking-details span{
    font-weight: 500;
}

.field-error{
    color: red;
    position: absolute;
    right: 16px;
    display: inline;
}

.field-error + input{
    background-color: #ffcdd2!important;
}

/* responsive */
@media (max-width: 640px) {
    .form-input-wrapper{
        width: 100%;
    }

    .booking-details p, .btn{
        width: 100%;
        text-align: center;
    }
}